.section-parallax-bg {
  padding: 100px 0 80px;
  overflow: hidden;
  position: relative;
  color: #fff;
  text-align: center;

  .parallax-bg {
    position: absolute;
    width: 100%;
    height: 170%;
    left: 0;
    top: 0;
    right: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transform-origin: center center 0;
    transition: all 0.2s ease 0s;
  }

  p {
    font-family: $font-family-2;
    font-weight: 300;
    font-size: 16px;
  }

  .container {
    position: relative;
  }

  .section-title {
    color: #fff;
    padding-bottom: 34px;

    &:after {
      background: #fff;
    }
  }
}


